  transition: background-color 0.3s ease;
}
body.dark-mode {
  background-color: #1a1919;
  color: #999;
}

.dark-mode .w3-button.w3-white {
  background-color: #2196f3 !important;
  border-color: transparent !important;
  border-radius: 2px;
}

.app {
  max-width: 500px;
  margin: 16px auto;
  position: relative;
}

.dark-mode .app {
  background-color: #232323;
}

.dark-mode-toggle {
  position: absolute;
  right: 16px;
  top: 20px;
}
.dark-mode-toggle > button {
  font-size: 1.2em;
  background: none;
  border: none;
  color: #ffe600;
  cursor: pointer;
  transition: color 0.3s ease;
}
.dark-mode .dark-mode-toggle > button {
  color: #999;
}
.dark-mode-toggle > button:last-child {
  color: #666;
}
.dark-mode .dark-mode-toggle > button:last-child {
  color: #ffe600;
}
/* bab3fd */
.dark-mode-toggle > button:focus {
  outline: none;
}

.toggle-control {
  position: relative;
  padding: 0 4px;
}

input[type='checkbox'].dmcheck {
  width: 40px;
  height: 10px;
  background: #555;
  position: relative;
  border-radius: 5px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  cursor: pointer;
  vertical-align: 2px;
  outline: none;
}
input[type='checkbox'].dmcheck:focus-visible {
  outline: solid 2px white;
}
input[type='checkbox'].dmcheck + label {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  transition: all 0.3s ease;
  cursor: pointer;
  position: absolute;
  top: -1px;
  left: 3px;
  background: #fff;
  opacity: 0.8;
}

input[type='checkbox'].dmcheck:checked + label {
  left: 27px;
}

.pre {
  border-radius: 2px;
}

h4 button {
  margin-left: 10px;
}

.select-wrapper {
  position: relative;
}

/* add up/down arrows for select */
.select-wrapper::before,
.select-wrapper::after {
  content: '\25BC';
  color: #666;
  display: inline-block;
  position: absolute;
  right: 8px;
  top: 19px;
  pointer-events: none;
  font-size: 0.5em;
}
.select-wrapper::after {
  top: 10px;
  -webkit-transform: rotate(-180deg);
  transform: rotate(-180deg);
}

.select-wrapper > .w3-select {
  background-color: transparent;
  -webkit-appearance: none; /* also removes arrows */
  -moz-appearance: none;
  appearance: none;
  padding: 8px;
  border-radius: 0;
}
.select-wrapper > .w3-select::-ms-expand {
  display: none;
}

.w3-bar {
  margin-top: 32px;
}

.w3-select,
.w3-input {
  background-color: transparent;
  xborder-top-left-radius: 5px;
  xborder-top-right-radius: 5px;
}

.dark-mode .w3-select,
.dark-mode .w3-input {
  background-color: #121212;
  color: #666;
}
.dark-mode .w3-select:focus,
.dark-mode .w3-input:focus {
  color: #dfdfdf;
}

.w3-select:disabled,
.w3-input:disabled {
  color: #545454;
  background-color: #ebebe5;
}

.dark-mode .w3-select:disabled,
.dark-mode .w3-input:disabled {
  color: #000;
  background-color: #666;
}

